@import '../../css-config/mixins.scss';

.main-header {
  width: 100%;
  padding: 0 20px;
  z-index: 99;
  background-color: #fff;
  color: #000;
  box-shadow: 0px 5px 7px 0px #dbdbdb;
  // border-bottom: 1px solid;
  // border-color: rgba(0, 0, 0, 0.1);
  @include md {
    padding: 0 40px;
  }
  .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    max-width: 1200px;
    margin: 0 auto;
    @include md {
      height: 80px;
    }
  }
  .left {
    font-size: 22px;
    font-weight: 700;
    cursor: pointer;
    @include md {
      font-size: 34px;
      position: absolute;
    }
  }
  .center {
    list-style-type: none;
    display: none;
    gap: 25px;

    @include md {
      display: flex;
      left: 50%;
      transform: translateX(40%);
    }
    li {
      font-size: 14px;
      font-weight: 600;
      text-transform: uppercase;
      cursor: pointer;
      &:hover {
        opacity: 0.6;
      }
    }
  }

  .right {
    display: flex;
    align-items: center;
    gap: 20px;
    @include md {
      gap: 25px;
    }
    svg {
      font-size: 20px;
      cursor: pointer;
      @include md {
        font-size: 24px;
      }
      &:hover {
        opacity: 0.6;
      }
    }
    .cart-icon {
      position: relative;
      span {
        min-width: 20px;
        text-align: center;
        background: #fb56c1;
        padding: 2.5px;
        position: absolute;
        top: -5px;
        right: -12px;
        font-size: 12px;
        line-height: 1;
        border-radius: 10px;
      }
    }
  }
  &.sticky-header {
    background-color: #fff;
    color: #212121;
    position: sticky;
    top: 0;
    transform: translateY(-60px);
    animation: StickyHeader 0.3s ease forwards;
    box-shadow: 0px 5px 7px 0px #dbdbdb;
  }
}

@keyframes StickyHeader {
  0% {
    transform: translateY(-60px);
  }
  100% {
    transform: translateY(0);
  }
}
